import React, { Component } from 'react';
import { connect } from 'react-redux'
import $ from 'jquery'

import { fetchPhoto } from '../actions'

import * as LoadingBarAction from './LoadingBar/actions'
import LoadingBar from './LoadingBar'
import Photos from './Photos'

class App extends Component {
    /**
     *  jQuery Ajax 
     */
    componentDidMount() {
        $(document).on('ajaxStart', this.showLoading)
        $(document).on('ajaxStop', this.hideLoading)
    }

    /**
     *  ActionCreate Redux 
     * fetchPhoto { type: 'FETCH_PHOTO', payload: promise }
     *  payload redux-promise-middleware 
     *  payloadmiddleware  fetch  { type: 'FETCH_PHOTO_PENDING' }
     * fetch  { type: 'FETCH_PHOTO_FULFILLED' }  { type: 'FETCH_PHOTO_REJECTED' }
     * middle ['_PENDING', '_FULFILLED', '_REJECTED']
     */
    fetchPhotos = () => {
        this.props.dispatch(fetchPhoto())
    }

    /**
     *  redux-promise-middle Component 
     * LoadingBar  action: showLoadinghideLoading
     */
    withMiddleware = () => {
        this.showLoading()

        setTimeout(() => {
            this.hideLoading()
        }, 1500)
    }

    /**
     *  jQuery ajax
     *  jQuery.ajax  componentDidMount 
     */
    jqAjax = () => {
        $.ajax({
            url: ''
        })
    }

    /**
     *  Loadingbar showLoading action
     */
    showLoading = () => {
        this.props.dispatch(LoadingBarAction.showLoading())
    }

    /**
     *  Loadingbar hideLoading action
     */
    hideLoading = () => {
        this.props.dispatch(LoadingBarAction.hideLoading())
    }

    render() {
        return (
            <div id="app">
                <LoadingBar />
                
                <div className="group">
                    <button onClick={ this.fetchPhotos }>Fetch Photos</button>
                    <button onClick={ this.withMiddleware }>with Middleware</button>
                    <button onClick={ this.jqAjax }>jQuery Ajax</button>
                </div>

                <Photos />
            </div>
        );
    }
}

export default connect()(App);


